.daily {
    position: relative;
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        h5 {
            margin: 0;
        }
        svg {
            &:hover {
                cursor: pointer;
                fill: red;
            }
        }
    }
    &__date-add-tweet {
        position: absolute;
        bottom: 5px;
        right: 5px;
        font-size: 10px;
    }
}